@use 'variables' as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin desktop-xl {
    @media (min-width: v.$desktop-xl) {
        @content;
    }
}
@mixin desktop-xxl{
    @media (min-width: v.$desktop-xxl){
        @content;
    }
}

@mixin boton($bgColor, $colorTexto:v.$negro) {
    background-color: $bgColor;
    padding: 1.5rem 2rem;
    color: $colorTexto;
    font-size: 1.8rem;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    width: fit-content;
    display: inline-block;

    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}
@mixin contenedor{
    // max-width: 120rem; //toma como ancho maximo 1200px
    // width: 95%; //si no alcanza 120rem, toma el 95% del ancho de la pantalla
    width: min(95%, 140rem); //esta funcion engloba lo anterior
    margin: 0 auto; //centrar el contenedor
}
@mixin contenedor-sm{
    width: min(95%, 60rem); //esta funcion engloba lo anterior
    margin: 0 auto; //centrar el contenedor
}
@mixin contenedor-md{
    width: min(95%, 80rem); //esta funcion engloba lo anterior
    margin: 0 auto; //centrar el contenedor
}
@mixin contenedor-xl{
    width: min(95%, 1024px); //esta funcion engloba lo anterior
    margin: 0 auto; //centrar el contenedor
}
@mixin contenedor-xxl{
    width: min(95%, 1250px);
    margin: 0 auto;
}
@mixin contenedor-xxxl{
    width: min(95%, 1450px);
    margin: 0 auto;
}
