@mixin menu-social($color, $size){
    
    &__enlace{
        &::before{
            color: $color;
            font-size: $size;
            display: inline-block;  //para aplicar padding
            font-style: normal; //iconos, no muy anchos
            text-rendering: auto; //propiedad de vectores, 
            -webkit-font-smoothing: antialiased;  //aplica antialising para vectores
            font-family: "Font Awesome 6 Brands"; //aplica la familia importada en el layout
        }
        &[href*="facebook.com"]::before { //selector de inclusión
            content: "\f39e";
        }
        &[href*="github.com"]::before {
            content: "\f09b";
        }
        &[href*="tiktok.com"]::before {
            content: "\e07b";
        }
        &[href*="instagram.com"]::before {
            content: "\f16d";
        }
        &[href*="youtube.com"]::before {
            content: "\f167";
        }
        &[href*="twitter.com"]::before {
            content: "\f099";
        }
        &[href*="wa.me"]::before {
            content: "\f232";
        }
    }
    &__ocultar{
        display: none;
    }
}
