html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.8rem;
  max-width: 100%;
  background-color: #ADD8E6;
}

@keyframes updown {
  0%, 50%, 100% {
    transform: translatey(0);
  }
  20% {
    transform: translatey(5px);
  }
  25% {
    transform: translatey(-20px);
  }
  30% {
    transform: translatey(20px);
  }
  35% {
    transform: translatey(-5px);
  }
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  margin: 0;
  display: block;
}

h1, h2, h3 {
  margin: 0;
}

picture {
  display: block;
}

