}
.sidebar__close {
  position: absolute;
  top: 50%;
  right: -30px;
  background-color: #fff;
  height: 60px;
  width: 60px;
  border-radius: 50%;
  box-shadow: 0 0 20px 20px rgba(0, 0, 0, 0.03);
