  content: "";
  height: 2px;
  background: #E9949A;
  width: 12rem;
  position: absolute;
  bottom: -2px;
  left: 0;
  transform: translateX(-50%);
  opacity: 0;
  transition: transform 0.4s ease-in-out, opacity 0.4s linear;
}
